import React from 'react';

const UiView = (props) => (

    <div className="nav" style={props.navStyle}>
        <button onClick={props.onNewClick} style={props.buttonStyle}>New Game</button>
        <button onClick={props.onDisplayClick} style={props.buttonStyle}>
            {(props.selectType == 'Image') ? 'Image' : 'Hints'} </button>
        <button onClick={props.onSolveClick} style={props.buttonStyle}>AI Helper</button>

        {/* <button onClick={props.onSizeClick} style={props.buttonStyle}>{props.selectSize} puzzle</button> */}

    </div>

);

export default UiView;


