import './index.css';
import thunkMiddleware from 'redux-thunk'
import { createLogger } from 'redux-logger'
import { createStore, applyMiddleware } from 'redux'
import { startNewPuzzle, requestSolution, getSolution, changeGridSize } from './actionCreators'
import appReducer from './reducers'
import Root from './components/Root'
import React from 'react'
import ReactDOM from 'react-dom';

import { render } from 'react-dom'

const loggerMiddleware = createLogger()

const initialState = {

    viewArea: { w: window.innerWidth, h: window.innerHeight },
    gridSize: 3,
    tiles: null,
    puzzleArea: { w: 768, h: 768 },
    displayType: 'Image',
    solution: null,
    isSolving: false,
    showNext: false,
    isShowingSolution: false,
    imgSrc: "/static/media/gopro299.3c6aa1cd.jpg",
    images: [],
    defaultImg: null,
    dragIndex: null,
    dragStart: null,
    dragOffset: null,
    dragArea: null,
    dropIndex: null

}

const store = createStore(
    appReducer,
    initialState,
    applyMiddleware(
        thunkMiddleware,
        loggerMiddleware
    )
)


store.dispatch(startNewPuzzle(3));

render(
    <Root store={store} />,
    document.getElementById('root')

);





